import {Component, h} from 'preact';
import {bind} from 'decko';

import style from './style.scss';

import * as selectModal from '../../utils/selectModal';

const $ = window.$;

class Input extends Component {
    constructor(props) {
        super(props);
        this.state = {
            inputType: '',
            selectModalId: '',
            isSelect: false,
            isCity: false,
            isReadOnly: false,
            isDate: false,
            inputEle: null
        };
    }

    static defaultProps = {
        value: '',
        /** text, password, number, select, city, date */
        type: 'text',
        placeholder: '',
        selectParams: [],
        defaultSelectIndex: null,
        className: '',
        onChange() {
        },
        onSelect() {
        },
        onCityChange() {
        },
        onDateChange() {
        }
    };

    componentDidMount() {
        switch (this.props.type) {
            case 'select':
                this.selectInit();
                break;
            case 'city':
                this.cityInit();
                break;
            case 'date':
                this.dateInit();
                break;
            default:
                this.setState({
                    inputType: this.props.type
                });
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.defaultSelectIndex !== null && nextProps.defaultSelectIndex !== undefined && nextProps.defaultSelectIndex !== this.props.defaultSelectIndex) {
            this.handleSelect('', nextProps.defaultSelectIndex);
        }
    }

    componentWillUnmount() {
        selectModal.destroy(this.state.selectModalId);
    }

    @bind
    handleChange(e) {
        this.props.onChange(e.target.value, e);
    }

    @bind
    handleClick(e) {
        switch (this.props.type) {
            case 'select':
                this.showSelect();
                break;
            case 'city':
                break;
            case 'date':
                break;
            default:
                break;
        }
    }

    @bind
    selectInit() {
        const selectModalId = selectModal.init({
            item: selectModal.renderDefaultSelect(this.props.selectParams),
            callback: this.handleSelect
        });
        this.setState({
            inputType: 'text',
            selectModalId,
            isSelect: true,
            isReadOnly: true,
            isCity: false,
            isDate: false
        });
        // 如果有 defaultSelectIndex, 那么 选中 defaultSelectIndex, 触发事件
        if (this.props.defaultSelectIndex !== null && this.props.defaultSelectIndex !== undefined) {
            this.handleSelect('', this.props.defaultSelectIndex);
        }
    }

    @bind
    handleSelect(selectId, index) {
        let res = this.props.selectParams[index];
        this.props.onChange(res.value);
        this.props.onSelect(res);
    }

    showSelect() {
        selectModal.show(this.state.selectModalId);
    }

    cityInit() {
        this.setState({
            inputType: 'text',
            isSelect: false,
            isCity: true,
            isDate: false,
            isReadOnly: true
        });

        $(this.state.inputEle).selectSite({
            event: 'tap',
            targetEvent: 'tap',
            callback: this.cityCallback
        });
    }

    @bind
    cityCallback(params) {
        this.props.onChange(this.state.inputEle.value);
        this.props.onCityChange(params);
    }

    @bind
    initInputEle(input) {
        this.setState({
            inputEle: input
        });
    }

    @bind
    dateInit() {
        this.setState({
            inputType: 'text',
            isSelect: false,
            isCity: false,
            isDate: true,
            isReadOnly: true
        });

        $(this.state.inputEle).mdater({
            minDate: this.props.minDate,//最小日期
            maxDate: this.props.maxDate,//最大日期
            event: 'tap',//选择框内事件
            targetEvent: 'tap',//input框触发事件
            callback: this.dateCallback
        });
    }

    dateCallback(time) {
        this.props.onChange(this.state.inputEle.value);
        this.props.onDateChange(time);
    }

    render() {
        let arrows = '';

        if (this.props.type === 'select') {
            arrows = (<div className={style['input-arrows-container']}><img className={style['input-arrows']}
                                                                            src={require('../../assets/images/icon/arrows.png')}/>
            </div>);

        }

        return (
            <div onClick={this.handleClick} className={[style['input-container'], this.props.className].join(' ')}>
                <input className={style['input-el']}
                       ref={this.initInputEle}
                       value={this.props.value}
                       type={this.state.inputType}
                       onInput={this.handleChange}
                       readOnly={this.state.isReadOnly}
                       disabled={this.state.isReadOnly}
                       placeholder={this.props.placeholder}/>
                {arrows}
            </div>
        );
    }
}

export default Input;



