import {Component, h} from 'preact';
import {bind} from 'decko';
import {route} from 'preact-router';
import Promise from 'promise';

import Cell from '../../components/Cell/index';
import UploadCell from '../../components/UploadCell/index';

import {getInformationFormData, setInformationFormData} from '../../store/index';

import BaseRequest from '../../request/BaseRequest';

import style from './style.scss';

const Utils = window.Utils;
const Native = window.Native;

/**
 * 2. 填写投保信息
 */

class InformationFrom extends Component {
    constructor(props) {
        super(props);

        this.state = {
            type: '',
            typeLabelText: '',
            typeSelected: {},
            city: '',
            plateNumber: '',
            contactsName: '',
            applicantName: '',
            insuredName: '',
            contactPhone: '',
            expirationTime: '',
            identityCard: '',
            identityCardLabel: '请上传身份证',
            licensePic: '',
            typeDefaultIndex: null,
            typeParams: [{
                id: '0',
                value: '个体司机'
            }, {
                id: '1',
                value: '物流企业'
            }],
            identityCardEl: null,
            licensePicEl: null
        };
    }

    componentDidMount() {
        Native.sendPV('informationForm');
        Utils.setTitle('填写投保信息');

        let bufferData = getInformationFormData();
        if (bufferData) {
            this.setState({
                ...this.state,
                ...bufferData
            });
        } else {
            // 司机默认选中个人, 货主默认选中物流企业
            const client = Utils.getUrlParams().client;

            switch (client) {
                case 'driver':
                    this.setState({
                        typeDefaultIndex: 0
                    });
                    break;
                case 'cargo':
                    this.setState({
                        typeDefaultIndex: 1
                    });
            }

            BaseRequest.getTruckInfo().then((res) => {
                const data = res;
                this.setState({
                    plateNumber: data.plateNumber,
                    // contactsName: data.realname,
                    contactPhone: data.mobile,
                    city: data.city,
                    identityCard: data.picIdentityCard,
                    licensePic: data.picDrivingLicense
                });
            });
        }
    }

    componentWillUnmount() {
        setInformationFormData({
            ...this.state
        });
    }

    @bind
    handleInputChange(key, value) {
        this.setState({
            [key]: value
        });
    }

    validateForm() {
        let errors = [];
        if (!this.state.type) {
            errors.push('请选择投保类型');
        }
        if (!this.state.city) {
            errors.push('请选择投保城市');
        }
        if (!this.state.plateNumber) {
            errors.push('请填写车牌号码');
        }
        if (!this.state.contactsName) {
            errors.push('请填写联系人姓名');
        }
        if (!this.state.contactPhone) {
            errors.push('请输入联系人电话');
        }
        if (!/^1[\d]{10}$/.test(this.state.contactPhone)) {
            errors.push('请输入正确的联系人电话');
        }
        /* if (!this.state.expirationTime) {
            errors.push('请填写上一年保险到期日');
        } */
        if (!this.state.identityCard) {
            if (parseInt(this.state.typeSelected.id, 10)) {
                errors.push('请上传营业执照');
            } else {
                errors.push('请上传身份证');
            }
        }

        if (!this.state.licensePic) {
            errors.push('请上传车辆行驶证');
        }
        if (errors.length) {
            Utils.toast(errors[0]);
        }
        return !errors.length;
    }

    @bind
    handleUserTypeChange(data) {
        this.setState({
            typeSelected: data
        });
        // 0 司机, 1 企业
        if (parseInt(data.id, 10)) {
            this.setState({
                identityCardLabel: '请上传营业执照'
            });
        } else {
            this.setState({
                identityCardLabel: '请上传身份证'
            });
        }
    }

    @bind
    toNext() {
        Utils.sendTrack('click', 'informationSubmit');
        if (this.validateForm()) {
            Utils.sendTrack('click', 'informationSubmitSuccess');
            route('/form/types');
        }
    }

    render() {
        return (
            <div className={style['from']}>
                <div className={style['cell-container']}>
                    <Cell required={true}
                          type="select"
                          value={this.state.type}
                          defaultSelectIndex={this.state.typeDefaultIndex}
                          selectParams={this.state.typeParams}
                          label="选择投保类型"
                          placeholder="选择投保类型"
                          onInputChange={(value) => {
                              this.handleInputChange('type', value);
                          }}
                          onSelectChange={this.handleUserTypeChange}/>
                    <Cell required={true}
                          value={this.state.city}
                          type="city"
                          label="选择投保城市"
                          placeholder="请选择投保城市"
                          onInputChange={(value) => {
                              this.handleInputChange('city', value);
                          }}/>
                    <Cell required={true}
                          value={this.state.plateNumber}
                          label="车牌号码"
                          placeholder="请填写车牌号码"
                          onInputChange={(value) => {
                              this.handleInputChange('plateNumber', value);
                          }}/>
                    <Cell required={true}
                          value={this.state.contactsName}
                          label="联系人姓名"
                          placeholder="请填写联系人姓名"
                          onInputChange={(value) => {
                              this.handleInputChange('contactsName', value);
                          }}/>
                    <Cell value={this.state.applicantName}
                          label="投保人"
                          placeholder="与联系人一致可不填写"
                          onInputChange={(value) => {
                              this.handleInputChange('applicantName', value);
                          }}/>
                    <Cell value={this.state.insuredName}
                          label="被保险人"
                          placeholder="与联系人一致可不填写"
                          onInputChange={(value) => {
                              this.handleInputChange('insuredName', value);
                          }}/>
                    <Cell required={true}
                          value={this.state.contactPhone}
                          label="联系人电话"
                          placeholder="请填写联系人电话"
                          onInputChange={(value) => {
                              this.handleInputChange('contactPhone', value);
                          }}/>
                    <Cell value={this.state.expirationTime}
                          type="date"
                          label="上一年保险到期日"
                          placeholder="请填写保险到期日"
                          onInputChange={(value) => {
                              this.handleInputChange('expirationTime', value);
                          }}/>
                    <UploadCell required={true}
                                ref={(ref) => {
                                    this.state.identityCardEl = ref;
                                }}
                                value={this.state.identityCard}
                                onChange={(value) => {
                                    this.handleInputChange('identityCard', value);
                                }}
                                previewImg={this.state.identityCard || require('../../assets/images/upload/identity-card.png')}
                                uploadPicImg={require('../../assets/images/upload/upload-identity.png')}
                                label={this.state.identityCardLabel}/>
                    <UploadCell required={true}
                                ref={(ref) => {
                                    this.state.licensePicEl = ref;
                                }}
                                value={this.state.licensePic}
                                onChange={(value) => {
                                    this.handleInputChange('licensePic', value);
                                }}
                                previewImg={this.state.licensePic || require('../../assets/images/upload/driving-license.png')}
                                uploadPicImg={require('../../assets/images/upload/upload-driving.png')}
                                label="请上传车辆行驶证"/>
                    <Cell className="123"
                          label={<div className={style['tips']}>注意：带<span className={style['tips-icon']}>*</span>为必填项目
                          </div>}/>
                </div>
                <div className={style['fix-button']}>
                    <button onClick={this.toNext}>下一步</button>
                </div>
            </div>
        );
    }
}

export default InformationFrom;



