import {h, Component} from 'preact';
import {bind} from 'decko';
import Promise from 'promise';

import Label from '../../components/Label/index';

import style from './style.scss';
import cellStyle from '../Cell/style.scss';
import BaseRequest from '../../request/BaseRequest';

class UploadCell extends Component {
    constructor(props) {
        super(props);
        this.state = {
            fileInputEl: null,
            previewImg: '',
            file: []
        };
    }

    static defaultProps = {
        value: '',
        label: '',
        required: false,
        previewImg: null,
        uploadPicImg: null
    };

    componentDidMount() {
        this.setState({
            previewImg: this.props.previewImg
        });
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.value) {
            this.createImage(nextProps.value).then(img => {
                this.setState({
                    previewImg: img
                });
            });
        }

        if (this.props.previewImg !== nextProps.previewImg) {
            this.setState({
                previewImg: nextProps.previewImg
            });
        }
    }

    @bind
    handleInputChange(value) {
        this.props.onInputChange(value);
    }

    @bind
    fileInputInit(fileInputEl) {
        this.setState({
            fileInputEl
        });
    }

    @bind
    handleClick() {
        this.state.fileInputEl.click();
    }

    @bind
    handleFileChange(e) {
        let files = e.target.files || e.dataTransfer.files;
        if (!files.length) {
            return;
        }
        this.props.onChange(files);
        this.createImage(files).then(img => {
            this.setState({
                previewImg: img
            });
        });
    }

    @bind
    upload() {
        let formData = new FormData();
        for (let i = 0; i < this.state.file.length; i++) {
            formData.append('images', this.state.file[i]);
        }
        BaseRequest.upload(formData).then(resp => {
            console.log(resp);
        });
    }

    createImage(file) {
        return new Promise((resolve, reject) => {
            let result = [];
            let img = [];
            let error = false;
            let len = file.length;
            let loadLength = 0;

            for (let i = 0; i < len; i++) {
                let reader = new FileReader();
                reader.readAsDataURL(file[i]);
                reader.onload = (e) => {
                    img.push(e.target.result);
                    loadLength++;
                    if (loadLength === len && !error) {
                        result = img;
                        this.state.file = file;
                        resolve(result);
                    }
                };
                if (!/^image\/.*$/.test(file[i].type)) {
                    error = true;
                }
            }
            if (error) {
                window.Utils.toast('请选择图片');
            }
        });
    }

    render() {
        return (
            <div className={[style['upload-cell-container'], cellStyle['cell-line']].join(' ')}
                 onClick={this.handleClick}>
                <div className={cellStyle['cell-container']}>
                    <div className={cellStyle['cell-label']}><Label
                        required={this.props.required}>{this.props.label}</Label>
                    </div>
                    <div className={cellStyle['cell-body']}>
                    </div>
                </div>
                <div className={style['upload-container']}>
                    <div className={style['upload-preview']}>
                        <img className={style['upload-preview-img']}
                             src={this.state.previewImg}/>
                    </div>
                    <div className={style['upload-pic']}>
                        <img className={style['upload-pic-img']}
                             src={this.props.uploadPicImg}/>
                    </div>
                </div>
                <input type="file"
                       className="file-input"
                       style="display: none;"
                       onChange={this.handleFileChange}
                       ref={this.fileInputInit}/>
            </div>
        );
    }
}

export default UploadCell;



