import {h, Component} from 'preact';
import {bind} from 'decko';

import style from './style.scss';

class Checkbox extends Component {
    constructor(props) {
        super(props);
    }

    static defaultProps = {
        className: '',
        value: false,
        required: false,
        disabled: false,
        onChange() {
        }
    };

    @bind
    handleClick() {
        if (!this.props.disabled) {
            this.props.onChange(!this.props.value);
        }
    }

    render() {
        let activityClassName = '';
        if (this.props.value) {
            activityClassName = style['checked'];
        }
        return (<div className={[style['checkbox'], this.props.className, activityClassName].join(' ')}
                     onClick={this.handleClick}><span
            className={style['checkbox-icon']}/>{this.props.children}</div>);
    }
}

export default Checkbox;



