import {h, Component} from 'preact';
import {bind} from 'decko';

import Label from '../../components/Label/index';
import Input from '../../components/Input/index';
import Checkbox from '../../components/Checkbox/index';

import style from './style.scss';

class Cell extends Component {
    constructor(props) {
        super(props);
    }

    static defaultProps = {
        placeholder: '',
        value: '',
        label: '',
        required: false,
        type: 'text',
        selectParams: [],
        className: '',
        labelWidth: '',
        checkboxDisabled: false,
        onInputChange() {
        },
        onCheckboxChange() {
        },
        onSelectChange() {
        }
    };

    componentDidMount() {
        // console.log(this.props);
    }

    @bind
    handleInputChange(value) {
        this.props.onInputChange(value);
    }

    render() {
        let checkbox = null;
        if (this.props.checkboxValue !== null && this.props.checkboxValue !== undefined) {
            checkbox = <Checkbox value={this.props.checkboxValue}
                                 onChange={this.props.onCheckboxChange}
                                 disabled={this.props.checkboxDisabled}
                                 className={style['cell-checkbox-el']}>不记免赔</Checkbox>;
        }
        return (
            <div className={[style['cell-container'], style['cell-line'], this.props.className].join(' ')}>
                <div className={style['cell-label']}
                     style={{width: this.props.labelWidth}}><Label labelWidth={this.props.labelWidth}
                                                                   required={this.props.required}>{this.props.label}</Label>
                </div>
                <div className={style['cell-body']}>
                    {checkbox}
                    <div className={style['cell-input-container']}>
                        <Input className={style['cell-input-el']}
                               value={this.props.value}
                               type={this.props.type}
                               selectParams={this.props.selectParams}
                               placeholder={this.props.placeholder}
                               defaultSelectIndex={this.props.defaultSelectIndex}
                               onChange={this.handleInputChange}
                               onSelect={this.props.onSelectChange}/>
                    </div>
                </div>
            </div>
        );
    }

}

export default Cell;



