import {h, Component} from 'preact';

import style from './style.scss';

class Label extends Component {
    static defaultProps = {
        required: false
    };

    render() {
        let required = '';
        if (this.props.required) {
            required = <span className={style['label-required']}>*</span>;
        }

        return (
            <span className={style['label-container']}>{required}{this.props.children}</span>
        );
    }
}

export default Label;



