import {h, Component} from 'preact';
import {route} from 'preact-router';

import style from './style.scss';

const Utils = window.Utils;
const Native = window.Native;

/**
 * 介绍页
 */

class Introduce extends Component {
    constructor(props) {
        super(props);
    }

    toNext() {
        Utils.sendTrack('click', 'indexToNext');
        route('/form/information');
    }

    componentDidMount() {
        Native.sendPV('index');
        Utils.setTitle('货车帮车险');
    }

    render() {
        return (
            <div id="introduce" className={style['introduce-container']}>
                <img className={style['full-img']} src={require('../../assets/images/introduce/introduce_1.png')}
                     alt=""/>
                <img className={style['full-img']} src={require('../../assets/images/introduce/introduce_2.png')}
                     alt=""/>
                <img className={style['full-img']} src={require('../../assets/images/introduce/introduce_3.png')}
                     alt=""/>
                <img className={style['full-img']} src={require('../../assets/images/introduce/introduce_4.png')}
                     alt=""/>
                <img className={style['full-img']} src={require('../../assets/images/introduce/introduce_5.png')}
                     alt=""/>
                <img className={style['full-img']} src={require('../../assets/images/introduce/introduce_6.png')}
                     alt=""/>
                <div className={style['introduce-info']}>
                    <p className={style['introduce-phone']}>客服电话：<span
                        className={style['text-primary']}>028-67678422</span></p>
                    <p className={style['introduce-tips']}>温馨提示：需上年保单到期日在近2个月之内</p>
                </div>
                <img onClick={this.toNext} className={[style['full-img'], style['fix-bottom-btn']].join(' ')}
                     src={require('../../assets/images/introduce/introduce_7.png')} alt=""/>
            </div>
        );
    }
}

export default Introduce;



