import {Component, h} from 'preact';
import {bind} from 'decko';
import Promise from 'promise';
import {route} from 'preact-router';

import Cell from '../../components/Cell/index';
import Modal from '../../components/Modal/index';

import style from './style.scss';
import {getTypeFormData, setTypeFormData} from "../../store/index";
import {getInformationFormData} from '../../store/index';
import BaseRequest from '../../request/BaseRequest';
import InformationRequest from '../../request/InformationRequest';

const Utils = window.Utils;
const Native = window.Native;

/**
 * 选择投保险种
 */
class TypesForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            defaultLabelWidth: '3rem',
            trafficInsurance: '',
            trafficInsuranceSelected: {},
            trafficInsuranceParams: [{
                id: '1',
                value: '投保'
            }, {
                id: '0',
                value: '不投保'
            }],
            vehicleLossInsurance: '',
            vehicleLossInsuranceSelected: {},
            vehicleLossInsuranceSdfw: false,
            vehicleLossInsuranceParams: [{
                id: '1',
                value: '投保'
            }, {
                id: '0',
                value: '不投保'
            }],
            thirdPartyInsurance: '',
            thirdPartyInsuranceSelected: '',
            thirdPartyInsuranceSdfw: false,
            thirdPartyInsuranceParams: [{
                id: '50',
                value: '50万'
            }, {
                id: '100',
                value: '100万'
            }, {
                id: '150',
                value: '150万'
            }, {
                id: '0',
                value: '不投保'
            }],
            driverLiabilityInsurance: '',
            driverLiabilityInsuranceSelected: {},
            driverLiabilityInsuranceSdfw: false,
            driverLiabilityInsuranceParams: [{
                id: '5',
                value: '5万'
            }, {
                id: '10',
                value: '10万'
            }, {
                id: '20',
                value: '20万'
            }, {
                id: '30',
                value: '30万'
            }, {
                id: '50',
                value: '50万'
            }, {
                id: '0',
                value: '不投保'
            }],
            passengerLiabilityInsurance: '',
            passengerLiabilityInsuranceSelected: {},
            passengerLiabilityInsuranceSdfw: false,
            passengerLiabilityInsuranceParams: [{
                id: '5',
                value: '5万'
            }, {
                id: '10',
                value: '10万'
            }, {
                id: '20',
                value: '20万'
            }, {
                id: '30',
                value: '30万'
            }, {
                id: '50',
                value: '50万'
            }, {
                id: '0',
                value: '不投保'
            }],
            theftInsurance: '',
            theftInsuranceSelected: {},
            theftInsuranceSdfw: false,
            theftInsuranceParams: [{
                id: '1',
                value: '投保'
            }, {
                id: '0',
                value: '不投保'
            }],
            selfIgnitionLossInsurance: '',
            selfIgnitionLossInsuranceSelected: {},
            selfIgnitionLossInsuranceSdfw: false,
            selfIgnitionLossInsuranceParams: [{
                id: '1',
                value: '投保'
            }, {
                id: '0',
                value: '不投保'
            }],
            otherInsurance: '',
            vehicleVesselTax: '',
            vehicleVesselTaxSelected: {},
            vehicleVesselTaxParams: [{
                id: '1',
                value: '是'
            }, {
                id: '0',
                value: '否'
            }],
            modal: {
                question: {
                    value: false,
                    body: '建议增加其他商业险种再询价',
                    confirmText: '继续询价',
                    cancelText: '取消'
                }
            }
        };
    }

    componentDidMount() {
        Native.sendPV('typesForm');
        Utils.setTitle('填写投保信息');

        let bufferData = getTypeFormData();
        if (bufferData) {
            this.setState({
                ...this.state,
                ...bufferData
            });
        }
    }

    componentWillUnmount() {
        setTypeFormData({
            ...this.state
        });
    }

    @bind
    handleInputChange(key, value) {
        this.setState({
            [key]: value
        });
    }

    @bind
    handleSelectChange(key, checkbox, value) {
        // 判断是否改变
        let idStatus = !!parseInt(value.id, 10);
        let oldStatus = !!parseInt(this.state[key].id, 10);
        if (oldStatus !== idStatus) {
            this.setState({
                [checkbox]: idStatus
            });
        }
        this.setState({
            [key]: value
        });
    }

    @bind
    handleModalCancel() {
        this.setState({
            modal: {
                question: {
                    value: false
                }
            }
        });
    }

    @bind
    handleModalConfirm() {
        this.setState({
            modal: {
                question: {
                    value: false
                }
            }
        });
        this.submitForm();
    }

    @bind
    toNext() {
        /**
         * 当客户只选择了交强险；弹出提示：建议增加其他商业险种再询价；
         * 当客户只选择了商业三者险；弹出提示：建议增加其他商业险种再询价；
         * 当客户选择了交强险+商业三者险；弹出提示：建议增加其他商业险种再询价；
         */

        Utils.sendTrack('click', 'typeSubmit');

        const insuranceTypeList = [
            'trafficInsuranceSelected',
            'vehicleLossInsuranceSelected',
            'thirdPartyInsuranceSelected',
            'driverLiabilityInsuranceSelected',
            'passengerLiabilityInsuranceSelected',
            'theftInsuranceSelected',
            'selfIgnitionLossInsuranceSelected'
        ];

        // 判断有多少选中的
        const choseInsurances = insuranceTypeList.filter(item => {
            return parseInt(this.state[item].id, 10);
        });

        // 如果只有一个判断 是否是交强险| 商业第三者险
        // 如果两个  判断是否只有加强线和商业第三者险
        if ((choseInsurances.length === 1 && parseInt(this.state.trafficInsuranceSelected.id, 10)) || // 只选择了交强险
            (choseInsurances.length === 1 && parseInt(this.state.thirdPartyInsuranceSelected.id, 10)) || // 只选择了三者险
            (choseInsurances.length === 2 && parseInt(this.state.trafficInsuranceSelected.id, 10) && parseInt(this.state.thirdPartyInsuranceSelected.id, 10))) { // 只选择了交强险和第三者
            this.setState({
                modal: {
                    question: {
                        value: true
                    }
                }
            });
            Utils.sendTrack('click', 'typeSubmitError');
        } else {
            this.submitForm();
            Utils.sendTrack('click', 'typeSubmitSuccess');
        }
    }

    @bind
    submitForm() {
        Utils.loading.show();
        let informationFormData = getInformationFormData();
        Promise.all([this.uploadPic(informationFormData.identityCard), this.uploadPic(informationFormData.licensePic)]).then(res => {
            return this.submitInfo({
                ...this.state,
                ...informationFormData,
                idUrl: res[0],
                drivingLicenseUrl: res[1]
            });
        }).then(() => {
            Utils.loading.hide();
            route('/submitSuccess');
        }).catch(() => {
            Utils.loading.hide();
        });
    }

    submitInfo(data) {
        return InformationRequest.submitInfo({
            // 投保类型
            applyType: data.typeSelected.id,
            // 投保城市
            applyCity: data.city,
            // 车牌号
            plateNumber: data.plateNumber,
            // 联系人姓名
            contactName: data.contactsName,
            // 联系人手机号
            contactMobile: data.contactPhone,
            // 投保人姓名
            applicantName: data.applicantName || data.contactsName,
            // 被保人姓名
            insuredName: data.insuredName || data.contactsName,
            //上一年保单到期日
            perterminalTime: data.expirationTime,
            //身份证照片地址
            idUrl: data.idUrl,
            //行驶证照片地址
            drivingLicenseUrl: data.drivingLicenseUrl,
            //是否购买交强险
            trafficInsurance: data.trafficInsuranceSelected.id,
            // 车辆损失险0：未投保，1：已投保
            vehicleLossInsurance: data.vehicleLossInsuranceSelected.id,
            // 第三者损失险
            thirdPartyInsurance: data.thirdPartyInsuranceSelected.id,
            //司机责任险
            driverLiabilityInsurance: data.driverLiabilityInsuranceSelected.id,
            //乘客责任险，0：未投保，1：已投保，其他：投保金额
            passengerLiabilityInsurance: data.passengerLiabilityInsuranceSelected.id,
            //盗抢险
            theftInsurance: data.theftInsuranceSelected.id,
            //自燃损失险
            selfIgnitionLossInsurance: data.selfIgnitionLossInsuranceSelected.id,
            //是否缴纳车船税，0：缴纳，1：不缴纳
            vehicleVesselTax: data.vehicleVesselTaxSelected.id,
            //其他险种
            otherInsurance: data.otherInsurance,
            //不计免赔，依次对应车辆损失险、商业第三者险、司机责任险、乘客责任险、盗抢险、自燃损失险，0不投保，1投保
            noDeductibles: [
                data.vehicleLossInsuranceSdfw,
                data.thirdPartyInsuranceSdfw,
                data.driverLiabilityInsuranceSdfw,
                data.passengerLiabilityInsuranceSdfw,
                data.theftInsuranceSdfw,
                data.selfIgnitionLossInsuranceSdfw
            ].map(item => Number(item)).join(''),
            //来源
            source: Utils.getUrlParams().client
            // create,
        });
    }

    uploadPic(fileObject) {
        if (typeof fileObject === 'string') {
            return Promise.resolve(fileObject);
        }
        let formData = new FormData();
        for (let i = 0; i < fileObject.length; i++) {
            formData.append('images', fileObject[i]);
        }
        return BaseRequest.upload(formData);
    }

    render() {
        return (
            <div className={style['from']}>
                <div className={style['cell-container']}>
                    <Cell type="select"
                          value={this.state.trafficInsurance}
                          selectParams={this.state.trafficInsuranceParams}
                          defaultSelectIndex="0"
                          labelWidth={this.state.defaultLabelWidth}
                          label="交强险"
                          placeholder="请选择交强险"
                          onInputChange={(value) => {
                              this.handleInputChange('trafficInsurance', value);
                          }}
                          onSelectChange={res => {
                              this.handleInputChange('trafficInsuranceSelected', res);
                          }}/>
                    <Cell type="select"
                          value={this.state.vehicleLossInsurance}
                          selectParams={this.state.vehicleLossInsuranceParams}
                          checkboxValue={this.state.vehicleLossInsuranceSdfw}
                          checkboxDisabled={!parseInt(this.state.vehicleLossInsuranceSelected.id, 10)}
                          defaultSelectIndex="0"
                          label="车辆损失险"
                          labelWidth={this.state.defaultLabelWidth}
                          placeholder="请选择车辆损失险"
                          onCheckboxChange={(value) => {
                              this.handleInputChange('vehicleLossInsuranceSdfw', value);
                          }}
                          onInputChange={(value, index) => {
                              this.handleInputChange('vehicleLossInsurance', value);
                          }}
                          onSelectChange={res => {
                              this.handleSelectChange('vehicleLossInsuranceSelected', 'vehicleLossInsuranceSdfw', res);
                          }}/>
                    <Cell type="select"
                          value={this.state.thirdPartyInsurance}
                          selectParams={this.state.thirdPartyInsuranceParams}
                          defaultSelectIndex="1"
                          checkboxValue={this.state.thirdPartyInsuranceSdfw}
                          checkboxDisabled={!parseInt(this.state.thirdPartyInsuranceSelected.id, 10)}
                          label="商业第三者险"
                          labelWidth={this.state.defaultLabelWidth}
                          placeholder="请选择商业第三者险"
                          onCheckboxChange={(value) => {
                              this.handleInputChange('thirdPartyInsuranceSdfw', value);
                          }}
                          onInputChange={(value) => {
                              this.handleInputChange('thirdPartyInsurance', value);
                          }}
                          onSelectChange={res => {
                              this.handleSelectChange('thirdPartyInsuranceSelected', 'thirdPartyInsuranceSdfw', res);
                          }}/>
                    <Cell type="select"
                          value={this.state.driverLiabilityInsurance}
                          selectParams={this.state.driverLiabilityInsuranceParams}
                          defaultSelectIndex="5"
                          checkboxValue={this.state.driverLiabilityInsuranceSdfw}
                          checkboxDisabled={!parseInt(this.state.driverLiabilityInsuranceSelected.id, 10)}
                          label="司机责任险"
                          labelWidth={this.state.defaultLabelWidth}
                          placeholder="请选择司机责任险"
                          onCheckboxChange={(value) => {
                              this.handleInputChange('driverLiabilityInsuranceSdfw', value);
                          }}
                          onInputChange={(value) => {
                              this.handleInputChange('driverLiabilityInsurance', value);
                          }}
                          onSelectChange={res => {
                              this.handleSelectChange('driverLiabilityInsuranceSelected', 'driverLiabilityInsuranceSdfw', res);
                          }}/>
                    <Cell type="select"
                          value={this.state.passengerLiabilityInsurance}
                          selectParams={this.state.passengerLiabilityInsuranceParams}
                          defaultSelectIndex="5"
                          checkboxValue={this.state.passengerLiabilityInsuranceSdfw}
                          checkboxDisabled={!parseInt(this.state.passengerLiabilityInsuranceSelected.id, 10)}
                          label="乘客责任险"
                          labelWidth={this.state.defaultLabelWidth}
                          placeholder="请选择乘客责任险"
                          onCheckboxChange={(value) => {
                              this.handleInputChange('passengerLiabilityInsuranceSdfw', value);
                          }}
                          onInputChange={(value) => {
                              this.handleInputChange('passengerLiabilityInsurance', value);
                          }}
                          onSelectChange={res => {
                              this.handleSelectChange('passengerLiabilityInsuranceSelected', 'passengerLiabilityInsuranceSdfw', res);
                          }}/>
                    <Cell type="select"
                          value={this.state.theftInsurance}
                          selectParams={this.state.theftInsuranceParams}
                          defaultSelectIndex="1"
                          checkboxValue={this.state.theftInsuranceSdfw}
                          checkboxDisabled={!parseInt(this.state.theftInsuranceSelected.id, 10)}
                          label="盗抢险"
                          labelWidth={this.state.defaultLabelWidth}
                          placeholder="请选择盗抢险"
                          onCheckboxChange={(value) => {
                              this.handleInputChange('theftInsuranceSdfw', value);
                          }}
                          onInputChange={(value) => {
                              this.handleInputChange('theftInsurance', value);
                          }}
                          onSelectChange={res => {
                              this.handleSelectChange('theftInsuranceSelected', 'theftInsuranceSdfw', res);
                          }}/>
                    <Cell type="select"
                          value={this.state.selfIgnitionLossInsurance}
                          selectParams={this.state.selfIgnitionLossInsuranceParams}
                          defaultSelectIndex="1"
                          checkboxValue={this.state.selfIgnitionLossInsuranceSdfw}
                          checkboxDisabled={!parseInt(this.state.selfIgnitionLossInsuranceSelected.id, 10)}
                          label="自燃损失险"
                          labelWidth={this.state.defaultLabelWidth}
                          placeholder="请选择自燃损失险"
                          onCheckboxChange={(value) => {
                              this.handleInputChange('selfIgnitionLossInsuranceSdfw', value);
                          }}
                          onInputChange={(value) => {
                              this.handleInputChange('selfIgnitionLossInsurance', value);
                          }}
                          onSelectChange={res => {
                              this.handleSelectChange('selfIgnitionLossInsuranceSelected', 'selfIgnitionLossInsuranceSdfw', res);
                          }}/>
                    <Cell type="text"
                          value={this.state.otherInsurance}
                          label="其他险种"
                          placeholder="请填写其他险种"
                          onInputChange={(value) => {
                              this.handleInputChange('otherInsurance', value);
                          }}/>
                    <Cell type="select"
                          value={this.state.vehicleVesselTax}
                          selectParams={this.state.vehicleVesselTaxParams}
                          defaultSelectIndex="1"
                          label="是否缴纳车船税"
                          placeholder="请选择是否缴纳车船税"
                          onInputChange={(value) => {
                              this.handleInputChange('vehicleVesselTax', value);
                          }}/>
                </div>
                <div className={style['fix-button']}>
                    <button onClick={this.toNext}>我要最低报价</button>
                </div>
                <Modal {...this.state.modal.question} onCancel={this.handleModalCancel}
                       onConfirm={this.handleModalConfirm}/>
            </div>
        );
    }
}

export default TypesForm;



