import {h, Component} from 'preact';
import {bind} from 'decko';

import style from './style.scss';

const Utils = window.Utils;
const Native = window.Native;

/**
 * SubmitSuccess
 */

class SubmitSuccess extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        Native.sendPV('typesForm');
        Utils.setTitle('填写投保信息');
    }

    @bind
    handleCloseBtnClick() {
        window.Native.navigation.closeWindow();
    }

    render() {
        return (
            <div className={style['submit-success-container']}>
                <div className={style['success-icon-container']}>
                    <img className={style['success-icon']}
                         src={require('../../assets/images/icon/submit-success-icon.png')}/>
                </div>
                <p className={style['success-text']}>提交成功</p>
                <p className={style['success-info']}>完成报价以后,客服将和您取得联系</p>
                <button className={style['success-close-btn']}
                        onClick={this.handleCloseBtnClick}>关闭页面
                </button>
            </div>
        );
    }
}

export default SubmitSuccess;



