import selectModalStyle from '../assets/styles/baseSelectModal.scss';

const Utils = window.Utils;
const $ = window.$;

let selectModalCount = 1;

export const updateCount = (count = selectModalCount + 1) => {
    return selectModalCount = count;
};

export const getId = ({idName = 'selectModal', index = selectModalCount}) => {
    return [idName, index].join('_');
};

export const getSelectModalCount = () => {
    return selectModalCount;
};

export const init = ({id = getId({index: updateCount()}), item = '', needCancelBtn = false, callback, autoHide = true}) => {
    let cancelBtn = '';
    if (needCancelBtn) {
        cancelBtn = renderDefaultSelect([{
            id: 'base-cancel-btn',
            className: ['select-base-cancel'],
            value: '取消'
        }]);
    }

    Utils.select.init({
        id,
        cls: 'base-select',
        hideOnMaskTap: true,
        event: 'click',
        body: `${item}${cancelBtn}`,//内容
        bindEvent() {
            $('#' + id).on('click', '.select-base-item', function () {
                const selectElId = $(this).attr('data-id');
                const selectElIndex = $(this).attr('data-index');
                // 自动关闭开启点击选项关闭即可
                if (autoHide) {
                    hide(id);
                } else if (selectElId === 'base-cancel-btn') {
                    hide(id);
                }

                if (callback && selectElId !== 'base-cancel-btn') {
                    return callback(selectElId, selectElIndex);
                }
            });
        },
        confirm() {
        },
        cancel() {
        }
    });

    return id;
};
/**
 * [{id: '12', className: '12', value: HtmlString}]
 * @param items
 */
export const renderDefaultSelect = (items = []) => {
    return items.map(({id = '', className = [], value = ''}, index) => {
        return ['<div data-id="', id, '" data-index="', index, '" class="select-base-item ', selectModalStyle['select-base-item'], ' ', className.join(' '), '">', value, '</div>'].join('');
    }).join('');
};

export const show = (id) => {
    Utils.select.show(id);
};

export const hide = (id) => {
    Utils.select.hide(id);
};

export const destroy = (id) => {
    Utils.select.destroy(id);
};



