import xhr from '../utils/xhr';

export default new class {
    upload(data) {
        return xhr({
            url: '/insurance/upload-image.do',
            data,
            method: 'post',
            host: 'fisAdmin',
            needLogin: false
        });
    }

    login() {
        return xhr({
            url: '/sso/common/web/mobile/login.do',
            host: 'www',
            needLogin: false,
            auto: false,
            data: {
                mobile: 18988888777,
                password: 123123,
                domainId: 1,
                appClientId: 207,
                isPwdEncrypt: 0
            },
            method: 'post'
        });
    }

    getDrInfo(data) {
        return xhr({
            url: '/insurance/carrier/ht/get-vehicle-user.do',
            method: 'post',
            data
        });
    }

    getTruckInfo() {
        return xhr({
            url: '/insurance/freight/intent-truck-info.do',
            method: 'POST',
            data: {
                getPics: 1
            }
        });
    }
};



