let informationFormData = null;
let typeFormData = null;

export const getInformationFormData = () => {
    return informationFormData;
};

export const setInformationFormData = (data) => {
    informationFormData = {
        ...data
    };
};

export const getTypeFormData = () => {
    return typeFormData;
};

export const setTypeFormData = (data) => {
    typeFormData = {
        ...data
    };
};




